<template>
  <div class="community">
    <div class="community-head">影迷社区
      <div class="community-head-write" @click='writeArticle'></div>
    </div>
    <div class="community-nav">
      <router-link to='/community/recommend' active-class="current">推荐</router-link>
      <router-link to='/community/new'  active-class="current">最新</router-link>
    </div>

    <router-view/>
    
    <!-- 提示登录之后才可以写文章 -->
    <div class="promptToLogin" v-show='promptToLoginShow'>
      <main>
        <div class="close" @click='promptToLoginShow = false'></div>
        <header>当前你还没有登录</header>
        <button @click='$router.push("/login")'>立即登录</button>
      </main>
    </div>
    <!-- 写文章面板 -->
    <transition name='writeArticleInfo'>
    <div class="writeArticle" v-show='writePanelShow'>
      <div class="close" @click='writePanelShow = false'></div>
      <header>发帖</header>
      <div class="writeTitle">
        <input type="text" placeholder="文章标题" v-model='article.title'>
      </div>
      <main>
        <textarea  placeholder="内容" v-model='article.content'></textarea>
      </main>
      <button @click='submit'>发布</button>
      <footer @click='openFile'>
        <span >{{pic}}</span>
        <!-- <img :src="img" alt="" v-show='img' style='width:100%;height:100%;'> -->
        <input type="file" accept="image/*"  ref='getFileInput' webkitRelativePath @change='getFile($event)'>
      </footer>
      <div class="showFile">
        <img :src="url" alt="" style='width:100%;height:100%;'>
      </div>
    </div>
    </transition>

    <!-- 文章详情面板 -->
      <transition name='showArticleInfo'>
      <div class="article-info" v-show='isShowArticleInfo'>
        <div class="login-back" @click='isShowArticleInfo = false'></div>
        <header>文章详情</header>
        <main>
          <div class="article-info-author">
            <div class="article-info-authorHead">
              <img :src='require("../../../../../../Desktop/阿炳影视服务器/router/upload/"+user.headUrl)' alt="" style='width:100%;height:100%;border-radius:50%'>
            </div>
            <div class="article-info-authorName">{{articleInfo.user_name}}</div>
            <div class="article-info-authorfans">粉丝:0</div>
            <div class="article-info-followAuthor">+关注</div>
          </div>
          <div class="article-info-content">
            <div class="article-info-contentHead">{{articleInfo.article_title}}</div>
            <div class="article-info-contentBody">{{articleInfo.article_content}}</div>
          </div>
        </main>
        <div class="comment_panel">
          <ul>
            <li v-for='c in comment' :key='c.comment_id'>
              <span>{{c.user_nickname}}</span>
              <span>{{c.user_comment}}</span>
              <span> {{c.comment_like}}</span>
            </li>
            <li style='opacity:0'></li>
          </ul>
        </div>
        <footer>
          <div class="article-info-comment" @click='beginComment' >
            &nbsp;&nbsp;&nbsp;来指点一下吧
          </div>
          <div class="article-info-collect">
            <span>{{articleInfo.article_collect}}</span>
          </div>
          <div class="article-info-like" @click='likeThis'>
            <span>{{articleInfo.article_like}}</span>
          </div>
          <div class="article-info-transmit">
            <span>{{articleInfo.article_transmit}}</span>
          </div>  
        </footer>
        <!-- 评论面板 -->
        <div class="article-info-commentPanel" ref="comment" >
          <div class="hide" @click='closeComment'></div>
          <div class="input">
            <textarea placeholder="请输入你的神评" v-model='user_comment'></textarea>
          </div>
          <button @click='submitComment'>发表</button>
        </div>
        <div class="articlesIsLoading" v-show='isloading'>
          <span></span>
          <h5>加载中...</h5>
        </div>
        <!-- 点赞评论转发提示面板 -->
        <div class="likeCommentTransmit-promptPanel" v-show='isShowLikeCommentTransmit'>
          <span v-show='isShowLikeCommentTransmitSuccess' style='color:green'>   {{likeCommentTransmitMsg}}</span>
          <span v-show='!isShowLikeCommentTransmitSuccess' style='color:red'>   {{likeCommentTransmitMsg}}</span>
        </div>
      </div>
      </transition>

      <!-- 提示信息面板 -->
      <div class="write-prompt" v-show='prompt.isShow'>
        <span style='color:red' v-show='!prompt.success'> {{prompt.message}}</span>
        <span style='color:green' v-show='prompt.success'> {{prompt.message}}</span>
      </div>
  </div>
</template>

<script>

export default {
    name : 'Community',
    props : ['user'],
    data(){
      return {
        current : 'current',

        promptToLoginShow : false,

        writePanelShow : false,

        article : {
          title : '',
          content : ''
        },

        isShowArticleInfo : false, //打开帖子详情页
        //详情页内容属性
        articleInfo : {
          user_head : '',
          user_name : '',
          user_fans : '',
          article_title : '',
          article_content : '',
          article_collect : '',
          article_like : '',
          article_transmit : ''
        },
        //提示面板属性
        prompt :{
          message : '',
          isShow : false,
          success : false
        },
        isloading : false,//加载

        //点赞,转发，评论需要用到的id号中间数据
        storeId : '',
        //点赞，转发，评论防抖开关
        swicthOfUser : false,
        //弹出点赞,转发，评论提示面板
        isShowLikeCommentTransmit : false,
        likeCommentTransmitMsg : '',
        isShowLikeCommentTransmitSuccess : true,

        pic : '+',
        url : '',

        user_comment :'',
        //用户评论
        comment : [],

        //标记当前文章的id
        article_id : ''
      }
    },
    methods:{
      chose(){
        this.current = ' current'
      },
      //打开写文章面板
      writeArticle(){
        if(this.user.status == false){
          this.promptToLoginShow = true
        }else{
          this.writePanelShow = true
        }
      },
      //提交帖子内容
      submit(){
        if(this.article.title == '' || this.article == ''){
          this.prompt.isShow = true
          this.prompt.message = '输入内容不能有空'
          setTimeout(()=>{
            this.prompt.isShow = false
          },2000)
        }else{
          let now_date = Date.now();
          const articleMsg = 'user_nickname=' + this.user.nickname + '&' + 'user_phone=' + this.user.account + '&' + 'article_title='
          + this.article.title + '&' + 'article_content=' + this.article.content + '&' + 'publish_date=' + now_date
          this.$http.post('http://10.8.236.179:8081/api/writeArticle',articleMsg).then(
            response=>{
              if(response.data == 'error'){
                this.prompt.isShow = true
                this.prompt.message = '文章发布失败'
                setTimeout(()=>{
                  this.prompt.isShow = false
                },2000)
              }else{
                this.prompt.isShow = true
                this.prompt.success = true
                this.prompt.message = '文章发布成功'
                setTimeout(()=>{
                  this.prompt.isShow = false
                  this.prompt.success = false
                },2000)
                this.article.content = '';this.article.title = ''
              }
            },
            error=>{
              this.prompt.isShow = true
                this.prompt.message = '数据请求失败'
                setTimeout(()=>{
                  this.prompt.isShow = false
                },2000)
              console.log(error.message)
            }
          )
        }
      },
      //打开帖子详情
      checkArticleInfo(id){
        this.article_id = id
        if(this.user.status == true){
          this.swicthOfUser = false//重置点赞评论转发防抖阀
          this.storeId = id //将id号存储起来，以便点赞，评论，转发
          this.isloading = true
          this.isShowArticleInfo = true
          let articleId = 'article_id=' + id
          this.$http.post('http://10.8.236.179:8081/api/getArticleInfo',articleId).then(
            response=>{
              this.articleInfo.user_name = response.data[0].user_nickname
              this.articleInfo.user_fans = this.user.fans
              this.articleInfo.article_title = response.data[0].article_title
              this.articleInfo.article_content = response.data[0].article_content
              this.articleInfo.article_collect = response.data[0].article_collect
              this.articleInfo.article_like = response.data[0].article_like
              this.articleInfo.article_transmit = response.data[0].article_transmit

              //请求该文章所有评论
              this.$http.get(`http://10.8.236.179:8081/api/getAllComment?id=${id}`).then(
                response=>{
                  this.comment = response.data
                  this.isloading = false
                },
                error=>{
                  this.isloading = false
                  console.log(error.message)
                }
              ) 
            },
            error=>{
              console.log(error.message)
              this.isloading = false
            }
          )
        }else{
          this.promptToLoginShow = true
        }
      },
      //点赞该文章
      likeThis(){
        if(this.swicthOfUser == false){
          this.isloading = true
          this.$http.get(`http://10.8.236.179:8081/api/likeArticle?article_id=${this.storeId}`).then(
            response=>{
              this.isloading = false
              if(response.data == '成功点赞'){
                this.articleInfo.article_like = parseInt(this.articleInfo.article_like) + 1
                this.isShowLikeCommentTransmit = true
                this.likeCommentTransmitMsg = '点赞成功'
                //防抖开关打开
                this.swicthOfUser = true
                  setTimeout(()=>{
                    this.isShowLikeCommentTransmit = false
                  },2000)
              }else{
                this.isShowLikeCommentTransmit = true
                this.likeCommentTransmitMsg = '系统繁忙'
                this.isShowLikeCommentTransmitSuccess = false
                setTimeout(()=>{
                    this.isShowLikeCommentTransmit = false
                },2000)
              }
            },
            error=>{
              this.isloading = false
              console.log(error.message)
            }
          )
        }else{
                this.isShowLikeCommentTransmit = true
                this.likeCommentTransmitMsg = '请不要重复点赞'
                this.isShowLikeCommentTransmitSuccess = false
                setTimeout(()=>{
                    this.isShowLikeCommentTransmit = false
                    this.isShowLikeCommentTransmitSuccess = true
                },2000)
        }
      },
      //打开手机文件夹
      openFile(){
        this.$refs.getFileInput.click()
      },
      getFile(e){
        let file = e.target.files[0]
        this.pic = file.name
        //调用方法，转化成url格式
        // this.originUrl = this.getObjectURL(file)
        // console.log(this.originUrl)
        
        // let reader = new FileReader();  
        //   // 将图片2将转成 base64 格式  
        //   reader.readAsDataURL(file);  
        //   // 读取成功后的回调  
        //   reader.onloadend = function () {  
        //     let result = this.result;  
        //     let img = new Image();  
        //     img.src = result;  
        //     //判断图片是否大于100K,是就直接上传，反之压缩图片  
        //     if (this.result.length <= (100 * 1024)) {  
        //       self.headerImage = this.result;  
        //       self.postImg();  
        //     }else {  
        //       img.onload = function () {  
        //         let data = self.compress(img,Orientation);  
        //         self.headerImage = data;  
        //         self.postImg();  
        //       }  
        //     }  
        //   }   
        // }  
        // let param = new FormData()//创建form对象
        // param.append('file',file,file.name);//通过append向form对象添加数据
        // param.append('id',this.token)
        // console.log(file)
        // let config = {
        //   header : {
        //     'Content-Type':'multipart/form-data'
        //   }
        // },
        // this.$http.post('/uploadPicture',param,config).then(
        //   response=>{
        //     this.img = response.data.url
        //     this.newImg = response.data.url
        //   }
        // )
      },
      //打开评论板块
      beginComment(){
        this.$refs.comment.style.display = 'block'
      },
      closeComment(){
        this.$refs.comment.style.display = 'none'
      },
      //发表评论
      submitComment(){
        this.isloading = true
        let date = new Date();
        let commentMsg = 'id=' + this.article_id + '&' + 'user_phone=' + this.user.phone + '&' 
        + 'user_comment=' + this.user_comment + '&' + 'comment_like=0'+ '&' + 'user_nickname=' + this.user.nickname + '&'
        + 'comment_date=' + date 
        this.$http.post('http://10.8.236.179:8081/api/addComment',commentMsg).then(
          response=>{
            this.isloading = false
            if(response.data == '添加成功'){
              alert('添加成功')
              this.user_comment = ''
            }else{
              alert('添加失败')
            }
            //请求该文章所有评论
              this.$http.get(`http://10.8.236.179:8081/api/getAllComment?id=${this.article_id}`).then(
                response=>{
                  this.comment = response.data
                },
                error=>{
                  console.log(error.message)
                }
              ) 
          },
          error=>{
            this.isloading = false
            console.log(error.message)
          }
        )
      }
      // getObjectURL(file){
      //   let url = null
      //   url = window.createObjectURL(file)
      // }
    },
    mounted(){
      this.$bus.$on('checkArticleInfo',this.checkArticleInfo)
    }
}
</script>

<style>
.community{
  position: relative;
  height: 90vh;
  width: 100%;
}
.community-head{
  position: absolute; top: 0; left: 0;
  width: 100%;
  height: 14vw;
  text-align: center;
  line-height: 14vw;
  color: white;
  font-size: 18px;
  background: rgb(25, 182, 255);
}
.community-head-write{
  position: absolute; right: 2vw; top: 2vh;
  width: 4vh;
  height: 4vh;
  text-align: center;
  line-height: 4vh;
  font-size: 3vh;
  color: white;
}
.community-nav{
  position: absolute; top: 15vw; left: 0;
  width: 100%;
  height: 14vw;
  background: white;
  border-bottom: 1px solid rgb(209, 209, 209);
}
.community-nav a{
  position: absolute; top: 0;
  display: inline-block;
  box-sizing: border-box;
  transform: translateX(-50%);
  width: 18%;
  height: 100%;
  text-align: center;
  line-height: 14vw;
  font-size: 16px;
  color: rgb(105, 105, 105);
}
.community-nav a:nth-of-type(1){
  left: 25%;
}
.community-nav a:nth-of-type(2){
  left: 75%;
}
.community-nav a.current{
  color:rgb(25, 182, 255);
  border-bottom: 2px solid rgb(25, 182, 255);
}

/* 提示面板 */
.promptToLogin{
  position: absolute;left: 0; top: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.288);
  z-index: 3;
}
.promptToLogin main{
  position: absolute; left: 50%; top : 50%;
  transform : translate(-50%,-50%);
  width: 50vw;
  height: 50vw;
  background: white;
  border-radius: 5vw ;
}
.promptToLogin main header{
  position: absolute; left: 10%; top : 10vw;
  width: 80%;
  height: 20vw;
  text-align: center;
  line-height: 20vw;
  font-weight: bolder;
  color: rgb(35, 165, 216);
  font-size: 5vw;
}
.promptToLogin main button{
  position: absolute; top: 35vw; left: 20%;
  width: 60%;
  height: 10vw;
  background: skyblue;
  color: rgb(255, 255, 255);
}
.close{
  position: absolute; top: 5vw; right: 5vw;
  width: 5vw;
  height: 5vw;
  line-height: 5vw;
  font-size: 5vw;
  color: rgb(167, 167, 167);
}
/* 写文章面板 */
.writeArticle{
  position: absolute; top: 0; right: 0;
  width: 100vw;
  height: 100vh;
  background: white;
  z-index: 2;
  transition: all .3s ease-out;
}
.writeArticle header,main{
  width: 100vw;
  height: 15vw;
  text-align: center;
  line-height: 15vw;
  color: skyblue;
  font-weight: bolder;
  font-size: 18px;
  border-bottom: 1px solid rgb(231, 231, 231);
}
.writeTitle{
  width: 100vw;
  height: 12vw;
  border-bottom: 1px solid rgb(231, 231, 231);
}
.writeArticle input{
  margin-left: 5vw;
  width: 90vw;
  height: 12vw;
  line-height: 12vw;
  font-size: 5vw;
  color:rgb(134, 134, 134)
}
.writeArticle main{
  height:70vh;
  transition: all .1s;
}
.writeArticle main textarea{
  margin: 1vw 0 0 0;
  width: 90%;
  height: 68vh;
  font-size: 5vw;
  color:rgb(134, 134, 134);
  border:0;
  overflow-y: scroll;
  outline: 0;
}
.writeArticle button{
  position: absolute; right: 5vw; bottom: 5vw;
  width: 26vw;
  height: 10vw;
  text-align: center;
  line-height: 10vw;
  background: skyblue;
  color: white;
  transition: all .1s;
}
.writeArticle footer:before{
  display: inline-block;
  content:'选择封面图片';
  position: absolute; top: -5vw; left: -2vw;
  width: 20vw;
  height: 4vw;
  line-height: 4vw;
  font-size: 2vw;
  text-align: center;
}
.writeArticle .showFile{
  position: absolute; left: 30vw; bottom: 5vw;
  box-sizing: border-box;
  width: 16vw;
  height: 16vw;
  text-align: center;
  line-height: 15vw;
  color: rgb(78, 200, 248);
  font-size: 8vw;
  border: 1px solid rgb(78, 200, 248);
}
.writeArticle footer{
  position: absolute; left: 5vw; bottom: 5vw;
  box-sizing: border-box;
  width: 16vw;
  height: 16vw;
  text-align: center;
  line-height: 15vw;
  color: rgb(78, 200, 248);
  font-size: 8vw;
  border: 1px solid rgb(78, 200, 248);
}
.writeArticle footer input{
  display: none;
  width: 100%;
  height: 100%;
}
.writeArticleInfo-enter,.writeArticleInfo-leave-to{
  transform: translateY(-100vh);
}
.writeArticleInfo-enter-to,.writeArticleInfo-leave{
  transform: translateY(0);
}
/* 提示信息面板 */
.write-prompt{
  position: absolute; left: 50%; top : 0;
  margin-left: -20vw;
  width: 40vw;
  height: 20vw;
  text-align: center;
  line-height: 20vw;
  z-index: 4;
  font-weight: bolder;
  background: white;
  border: 1px solid rgb(209, 209, 209);
  border-radius: 2vw;
  animation: writePromptShow .3s linear 1;
}
/* 文章详情页 */
.article-info{
  position: absolute; top: 0; left: 0;
  width: 100vw;
  height: 100vh;
  background: white;
  z-index: 10;
  transition: all .3s ease-out;
}
.article-info header{
  width: 100vw;
  height: 15vw;
  line-height: 15vw;
  text-align: center;
  font-size: 4vw;
  font-weight:bolder;
  color: rgb(85, 85, 85);
  border-bottom: 1px solid rgb(182, 182, 182);
}
.article-info main{
  width: 100vw;
  height: 80vh;
  border: 0;
}
.article-info-author{
  position: relative;
  width: 100vw;
  height: 16vw;
}
.article-info-authorHead{
  position: absolute; left: 5vw;top: 2vw;
  width: 12vw;
  height: 12vw; 
  border-radius: 50%;
  background: rgb(131, 131, 131);
}
.article-info-authorName,.article-info-authorfans{
  position: absolute; left: 19vw;top: 3vw;
  width: 50vw;
  height: 4vw;
  font-size: 4vw;
  text-align: left;
  line-height: 4vw;
  color: rgb(92, 92, 92);
}
.article-info-authorfans{
  top: 9vw;
  font-size: 2vw;
}
.article-info-followAuthor{
  position: absolute; top: 4vw; right: 6vw;
  width: 20vw;
  height: 8vw;
  color: white;
  text-align: center;
  line-height: 8vw;
  border-radius: 4vw;
  background: rgb(53, 225, 255);
}
.article-info-content{
  width: 100vw;
  height: 120vw;
  /* background: red; */
}
.article-info-contentHead{
  margin-left: 5vw;
  width: 90vw;
  height: 15vw;
  color:black;
  font-size: 20px;
  font-weight: bolder;
  text-align: left;
}
.article-info-contentBody{
  margin-left: 5vw;
  width: 90vw;
  height: 105vw;
  color:black;
  text-align: left;
  font-size: 16px;
  line-height: 30px;
  text-indent: 2em;
  overflow-y: scroll;
}
.article-info footer{
  position: fixed; bottom: 0; left: 0;
  width: 100vw;
  height: 16vw;
  background: rgb(233, 233, 233);
}
.article-info-comment{
  position: absolute; left: 4vw; top : 4vw;
  width: 35vw;
  height: 8vw;
  text-align: center;
  line-height: 8vw;
  color: rgb(92, 92, 92);
  border-radius: 4vw;
  background: white;
}
.article-info-commentPanel{
  display: none;
  position: fixed; bottom: 0; left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.418);
}
.article-info-commentPanel .hide{
  position: absolute; top: 0; left: 0;
  width: 100vw;
  height: 100vh;
  min-height: 150vw;
  background: rgba(0, 0, 0, 0.438);
}
.article-info-commentPanel .input{
  position: absolute; left: 0;bottom: 0;
  width: 100vw;
  height: 45vw;
  background: white;
  box-shadow: 0 0 10px white;
}
.article-info-commentPanel .input textarea{
  position: absolute; top: 2vw; left: 5vw;
  width: 90vw;
  height: 30vw;
  font-size: 20px;
  border: 1px solid grey;
  user-select: none;
  outline: 2px solid skyblue;
}
.article-info-commentPanel button{
  position: absolute; right: 5vw; bottom:  3vw;
  width: 20vw;
  height: 8vw;
  color: white;
  background: skyblue;
}

.article-info-collect,.article-info-like,.article-info-transmit{
  position: absolute; top: 4vw; left: 55vw;
  width: 8vw;
  height: 8vw;
  text-align: center;
  font-size: 5vw;
  line-height: 8vw;
  border-radius: 50%;
  background: white;
}
.article-info-collect{
