<template>
  <div class="myArticle">
      <div class="login-back" @click='$router.back()'></div>

      <!-- 加载面板 -->
    <div class="articlesIsLoading" v-show='isloading'>
          <span></span>
          <h5>加载中...</h5>
    </div>
     <button @click='isDelete'>{{deleteButton}}</button>
    <ol class="myArticle-postList">
        <li v-for='p in my_allPosts' :key='p.article_id' >
            <header>{{p.article_title}}</header>
            <main></main>
            <footer>{{p.article_like}}</footer>
            <transition name='delete'>
            <div class="deleteIt" v-show='isShowDelete'>删除</div>
            </transition>
        </li>
    </ol>
  </div>
</template>

<script>
export default {
    name : 'MyArticle',
    props:['user'],
    data(){
        return {
            isloading : true,
            my_allPosts : [],
            my_post : {
                nickname:'',
                title:'',
                content:'',
                date:'',
                like:'',
                collect:'',
                transmit:''
            },
            isShowDelete : false,

            deleteButton : '批量删除'
        }
    },
    methods:{
        isDelete(){
            if(this.isShowDelete){
                this.isShowDelete = false
                this.deleteButton = '批量删除'
            }else{
                this.isShowDelete = true
                this.deleteButton = '取消删除'
            }
        }
    },
    mounted(){
        this.$http.get(`http://10.8.236.179:8081/api/checkMyArticle?user_phone=${this.user.account}`).then(
            response=>{
                this.isloading = false
                this.my_allPosts = response.data
            },
            error=>{
                this.isloading = false
                console.log(error.message)
            }
        )
    }
}
</script>

<style>
.myArticle{
    position: absolute;top:0;left: 0;
    width: 100vw;
    height: 100vh;
    background: white;
    z-index: 3;
    transition: all .3s ease-out;
    overflow: hidden;
}
.myArticle button{
    position: absolute; right: 5vw; top: 5vw;
    width: 17vw;
    height: 8vw;
    color: white;
    text-align: center;
    line-height: 8vw;
    background: skyblue;
}
.myArticle-postList{
    position: absolute; top: 10vh; left: 5vw;
    width: 90vw;
    height: 69vh;
    overflow-y: scroll;
    overflow-x: hidden;
}
.myArticle-postList li{
    box-sizing: border-box;
    float: left;
    margin:3vw 0 0 3.5vw;
    width: 25vw;
    height: 35vw;
    border:1px solid skyblue;
    border-radius: 0 5vw 0 0 ;
    box-shadow: 2px 2px 2px rgb(201, 201, 201),inset 2px 2px  white;
    overflow: hidden;
}
.myArticle-postList li header{
    margin: 3% 0 0 5%;
    width: 90%;
    height: 47%;
    color: rgb(23, 153, 204);
    font-weight: bolder;
}
.myArticle-postList li main,.myArticle-postList li footer{
    position: absolute; left: 50%; top : 50%;
    transform : translate(-50%,-50%);
    width: 8vw;
    height: 8vw;
    text-align: center;
    line-height: 8vw;
    color:rgb(224, 60, 60);
    border: 1px solid skyblue;
    border-radius: 50%;
}
.myArticle-postList li footer{
    top: 62%;
    color: rgb(58, 178, 226);
    border: 0;
}
.deleteIt{
    position: absolute; bottom: 0;left: 0;
    width: 100%;
    height: 6vw;
    color: white;
    line-height: 6vw;
    text-align: center;
    background: rgb(197, 37, 37);
    transition: all .2s linear;
}
.delete-enter,.delete-leave-to{
    transform: translateY(6vw);
}
.delete-enter-to,.delete-leave{
    transform: translateY(0);
}
</style>