<template>
  <div class="revise_password">
      <div class="login-back" @click='$router.back()'></div>
      <header>设置一个新的密码</header>
      <div class="set_newPassword">
          <span>设置新的密码:</span>
          <input type="password" ref="password" v-model='password'>
          <i @click='showPassword("open")' v-show='!isShowPassword' ></i>
          <i @click='showPassword("close")' v-show='isShowPassword' style='color:skyblue' ></i>
      </div>
      <div class="set_newRePassword">
          <span>重新输入密码:</span>
          <input type="password" ref='repassword' v-model='repassword'>
          <i @click='showRePassword("open")' v-show='!isShowRePassword' ></i>
          <i @click='showRePassword("close")' v-show='isShowRePassword' style='color:skyblue' ></i>
      </div>
      <button @click='revisePassword'>修 改 密 码</button>
      <!-- 提示弹窗 -->
      <div class="registerPrompt" v-show='isShowPrompt'><span style='color:red'></span>&nbsp;{{promptMsg}}</div>
  </div>
</template>

<script>
export default {
    name : 'RevisePassword',
    props : ['getId'],
    data(){
        return{
            isShowPassword : false,
            isShowRePassword : false,

            password : '',
            repassword : '',

            isShowPrompt : false,
            promptMsg : ''
        }
    },
    methods:{
        showPassword(value){
            if(value == 'open'){
                this.isShowPassword = true
                this.$refs.password.type = 'text'
            }else{
                this.isShowPassword = false
                this.$refs.password.type = 'password'
            }
        },
        showRePassword(value){
            if(value == 'open'){
                this.isShowRePassword = true
                this.$refs.repassword.type = 'text'
            }else{
                this.isShowRePassword = false
                this.$refs.repassword.type = 'password'
            }
        },
        //修改密码
        revisePassword(){
            if(this.password == this.repassword){
                let revisePassword = 'password=' + this.password + '&' + 'id=' + this.getId
                this.$http.post('http://10.8.236.179:8081/api/revisePassword',revisePassword).then(
                    response=>{
                        if(response.data == '修改成功'){
                            this.isShowPrompt = true
                            this.promptMsg = '密码修改成功'
                            setTimeout(()=>{
                                this.isShowPassword = false
                                this.$router.push('/login')
                            },800)
                            
                        }else{
                            this.isShowPrompt = true
                            this.promptMsg = '密码修改失败'
                            setTimeout(()=>{
                                this.isShowPassword = false
                            },1000)
                        }
                    }
                )
            }else{
                this.isShowPrompt = true
                this.promptMsg = '两次密码不一致'
                setTimeout(()=>{
                    this.isShowPassword = false
                },1000)
            }
            
        }
    },

}
</script>

<style lang='less'>
.revise_password{
    position: absolute; top:0;left: 0;
    width: 100vw;
    height: 100vh;
    background: white;
    transition: all .3s ease-out;
    z-index: 2;
    header{
        position: absolute ; top: 10vw; left: 5vw;
        width: 90vw;
        height: 10vw;
        font-size: 18px;
        font-weight: bolder;
        text-align: center;
        line-height: 10vw;
        color: skyblue;
        border-bottom: 1px solid skyblue;
    }
    .set_newPassword,.set_newRePassword{
        position: absolute; left: 5vw; top: 32vw;
        width: 90vw;
        height: 10vw;
        border-bottom: 1px solid skyblue;
        span{
            display: inline-block;
            text-align: right;
            line-height: 10vw;
            color: rgb(7, 158, 218);
        }
        input{
            margin-left: 3vw;
        }
        i{
            display: inline-block;
            margin-left: 5vw;
            color: rgb(143, 143, 143);
        }
    }
    .set_newRePassword{
        top: 45vw;
    }
    button{
        position: absolute; left: 5vw; top: 60vw;
        width: 90vw;
        height: 10vw;
        color: white;
        background: skyblue;
        border-radius: 1vw;
    }
}
</style>