<template>
<div>
      <!-- 筛选面板 -->
      <transition>
        <router-view  class="filter" />
      </transition>
       <div class="home">
        <div class="home-head">&nbsp;<i style='font-size:20px'>AB</i>&nbsp;<span style='color:white'>影视</span></div>
          <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide">
                  <img src="../../assets/海报1.jpg" alt="" style='width:100%;height:100%'>
                </div>
                <div class="swiper-slide">
                  <img src="../../assets/海报2.jpg" alt="" style='width:100%;height:100%'>
                </div>
                <div class="swiper-slide">
                  <img src="../../assets/海报3.jpg" alt="" style='width:100%;height:100%'>
                </div>
            </div>
            <!-- 如果需要分页器 -->
            <div class="swiper-pagination"></div>
          </div>
          <!-- 全部电影展示区域 -->
          <div class="home-classify">
            <div class="home-classify-nav">
                <div class="home-classify-nav-left" @click='scoreSort'>{{toggleText}}</div>
                <span></span>
                <router-link to='/filer_entries'><div class="home-classify-nav-right"></div></router-link>
            </div>
            <ul>
              <li v-for="f in films" :key='f.id' @click='queryDetails(f.id)'>
                  <div class="home-filmScore">{{f.film_score}}</div>
                  <img :src="f.film_post" alt="">
                  <div class="home-filmName">{{f.film_name}}</div>
              </li>
            </ul>
          </div>
  </div>
  <!-- 电影详情页 -->
  <div class="home-filmDetails" v-show="showFilmDetails">
    <div class="hone-closeFilmDetails" @click='showFilmDetails = !showFilmDetails'></div>
    <div class="home-filmDetails-post">
      <img :src="filmDetails.film_post" alt="" style='width:100%;height:100%'>
    </div>
    <div class="home-filmDetails-name">
        {{filmDetails.film_name}}
    </div><header>BD</header>
    <div class="home-filmDetails-time">
        发行年份：{{filmDetails.film_date}}
    </div>
    <div class="home-filmDetails-actors">
       主演：<a>{{filmDetails.film_actor}}</a>
    </div>
    <div class="home-filmDetails-director">
       导演：{{filmDetails.film_director}}
    </div>
    <div class="home-filmDetails-region">
       地区：{{filmDetails.film_region}}
    </div>
    <div class="home-filmDetails-type">
       地区：{{filmDetails.film_type}}
    </div>
    <div class="home-filmDetails-score">
       {{filmDetails.film_score}}% <span style='color:red'></span>
    </div>
    <div class="home-filmDetails-message">
       <span>剧情详情：</span><br>&nbsp;&nbsp;&nbsp;
       <span style="color:rgb(0, 90, 100)">{{filmDetails.film_message}}</span>
    </div>
  </div>
</div>
 
  
</template>

<script>
import Swiper from 'swiper'
import 'swiper/swiper-bundle.min.css'
import 'swiper/swiper-bundle.min.js'

import 'animation.css'
export default {    
    name : 'Home',
    props : ['films'],
    data(){
      return {
        // films :  []
        showFilmDetails : false,

        filmDetails : [],

        toggleHighToLow : 'default',
        toggleText : '默认排序'
      }
    },
    methods:{
      queryDetails(id){
        this.showFilmDetails = true
        this.films.forEach(f => {
          if(f.id === id){
            this.filmDetails = f
          }
        });   
      },
      scoreSort(){
        const arrow = document.querySelector('.home-classify-nav span')
        if(this.toggleHighToLow == 'default'){
          this.films.sort((before,after)=>{
            return  after.film_score - before.film_score
          })
          this.toggleText = '评分由高至低'
          arrow.style.opacity = 1
          arrow.classList.remove('active')
          this.toggleHighToLow = 'highToLow'
        }else if(this.toggleHighToLow == 'highToLow'){
          this.films.sort((before,after)=>{
            return  before.film_score - after.film_score
          })
          this.toggleText = '评分由低至高'
          arrow.style.opacity = 1
          arrow.classList.add('active')
          this.toggleHighToLow = 'lowToHigh'
        }else{
          this.films.sort((before,after)=>{
              return after.id - before.id
          })
          arrow.style.opacity = 0
          this.toggleText = '默认排序'
          this.toggleHighToLow = 'default'
        }
        
      },
    },
    watch:{

    },
    mounted(){
      var mySwiper = new Swiper ('.swiper-container', {
        // direction: 'vertical', // 垂直切换选项
        autoplay : true,
        loop: true, // 循环模式选项
        // 如果需要分页器
        pagination: {
          el: '.swiper-pagination',
        },
        // 如果需要滚动条
        scrollbar: {
          el: '.swiper-scrollbar',
        },  
        
      })  
      setInterval(()=>{
        mySwiper.slideNext(); 
      },5000)
    },
    beforeDestroy(){
      this.$bus.$emit('updataDataBase')
    }, 
}
</script>

<style>
.home{
  position: absolute;top: 0; left: 0;
  height: 90vh;
  width: 100%;
  background: rgb(246, 253, 255);
  overflow-y: scroll;
}
.home-head{
  position: fixed; top: 0; left: 0;
  width: 100%;
  height: 14vw;
  background: rgb(25, 182, 255);
  text-align: left;
  font-size: 16px;
  line-height: 14vw;
  font-weight: bolder;
  color: red;
  z-index: 2;
  text-shadow: 1px 1px 1px rgb(255, 148, 148);
}
.swiper-container {
    margin-top: 8vh;
    width: 100%;
    height: 54vw;
    background: rgba(255, 255, 255, 1);
} 
.home-classify{
  width: 100%;
  height: 58vh;
}
.home-classify-nav{
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 5vh;
  border-bottom: 1px solid skyblue;
}
.home-classify-nav-left{
  width: 100px;
  height: 100%;
  text-align: center;
  line-height: 5vh;
  font-weight: bolder;
  font-size: 1.1vh;
  color:rgb(0, 176, 199);
}
.home-classify-nav span{
  position: absolute; left: 90px; top: 0;
  width: 5vh;
  height: 5vh;
  text-align: center;
  line-height: 6vh;
  font-weight: bolder;
  font-weight: bolder;
  font-size: 3vh;
  color:rgb(0, 176, 199);
  transition: all .3s;
  opacity: 0;
}
.home-classify-nav span.active{
  transform: rotate(180deg);
}
.home-classify-nav-right{
  position: absolute; top: 0 ; right: 2vh;
  width: 5vh;
  height: 5vh;
  text-align: center;
  line-height: 5vh;
  font-size: 14px;
  color: rgb(0, 176, 199);
}
.home-classify ul{
  margin-top: -5vh;
}
.home-classify ul li{
  margin: 6vh 0 0 2vw;
  box-sizing: border-box;
  float: left;
  width: 22.5vw;
  height: 32.5vw;
  border: 1px solid skyblue;
  transition: all .3s;
}
.home-classify .home-filmScore{
  position: absolute; top: 0; right: 0;
  width: 20px;
  height: 20px;
  text-align: center;
  line-height: 20px;
  font-size: 2px;
  font-weight: bolder;
  color: rgb(107, 48, 0);
  background: rgb(255, 174, 0 , 0.9);
}
.home-classify ul li img{
  width: 100%;
  height: 100%;
}
.home-filmName{
  position: absolute; bottom: -3vh; left: 0;
  width: 22.5vw;
  /* height: 3vh; */
  height: 3vh;
  font-weight: bold;
  text-align: center;
  /* line-height: 3vh; */
}

.filter{
  transition: all .3s ease-out;
}
.v-enter,.v-leave-to{
    transform:translateY(-100vh)
}
.v-leave,.v-enter-to{
    transform:translateY(0)
}


</style>