<template>
    <!-- <transition name='animate__animated animate__bounce' enter-active-class="animate__fadeIn"> -->
        <div class="login">
            <div class="login-back" @click='$router.back()'></div>
            <div class="login-logo"></div>
            <div class="login-head">
                <ul>
                    <div :class="toggleSlide"></div>
                    <li @click='loginToggle("letter")'>手机短信登录</li>
                    <li @click='loginToggle("account")'>账号密码登录</li>
                </ul>
            </div>
            <div class="login-letter" v-show='loginLetter'>
                <div class="login-letter-head">
                    <div class="login-letter-headLeft">+86</div>
                    <input type="text" v-model='phoneLogin.phoneNumber'>
                    <div class="login-letter-headGet">获取验证码</div>
                </div>
                <div class="login-letter-idcode">
                    <div class="login-letter-idcode-left">输入验证码</div>
                    <input type="text" v-model='phoneLogin.idcode'>
                </div>
                <div :class="loginPhone">登 录</div>
            </div>
            <div class="login-account" v-show='!loginLetter'>
                <form @submit.prevent> 
                <div class="login-userAccount">
                    <input type="text" placeholder="请输入账号" v-model='passwordLogin.account' name='account'>
                </div>
                <div class="login-userPassword">
                    <input type="password" placeholder="请输入密码" v-model='passwordLogin.password' name='password'>
                    <span @click='showPassword("open")' v-show='!passwordShow'></span>
                    <span @click='showPassword("close")' v-show='passwordShow' style='color:skyblue'></span>
                </div>
                <button type="submit"  :class="loginAccount" @click='passwordLoginButton'>登 录</button>
                </form>
                <div class="login-userRegister" @click='registerAccount'>没有账号?注册</div>
                <div class="login-userForget" @click='retrieve'>忘记密码?</div>
            </div>
            <div class="login-more">
                <div class="login-more-mode"></div>
                <div class="login-more-mode"></div>
                <div class="login-more-mode"></div>
            </div>
            <div class="login-agreement">
                登录即表示你已阅读并同意 <a href="javascript:;" @click='checkAgreementBook'> 阿炳协议 </a> 和 <a href="javascript:;"> 隐私协议</a>
            </div>

            <!-- 提示弹窗 -->
            <div class="loginPrompt" v-show='promptShow'>{{promptMsg}}</div>

            <div class="loading" v-show='loading'>
                <div class="loadingSvg"></div>
            </div>
            <transition name='register'>
            <router-view/>
            </transition>
        </div>
    <!-- </transition> -->
</template>

<script>
import 'animate.css'
export default {
    name : 'Login',
    data(){
        return {
            phoneLogin : {
                phoneNumber : '',
                idcode : '',
                
            },
            passwordLogin : {
                account : '',
                password : ''
            },

            loginLetter : true,
            toggleSlide : 'toggleSlide',
            passwordShow : false,

            loginPhone : 'login-letter-button',
            loginAccount : 'login-userButton',

            promptShow : false,
            promptMsg : '',

            loading : false
        }
    },
    methods : {
        loginToggle(x){
            if(x == 'letter'){
                this.loginLetter = true
                this.toggleSlide = 'toggleSlide'
            }else if(x == 'account'){
                 this.loginLetter = false
                 this.toggleSlide = 'toggleSlide move'
            }
            
        },
        //切换账号密码登录方式或者手机登录方式
        showPassword(value){
            const pw = document.querySelector('.login-userPassword input')
            if(value === 'open'){
                this.passwordShow = true
                pw.type = 'text'
            }else{
                this.passwordShow = false
                pw.type = 'password'
            }
        },
        //账号密码登录按钮
        passwordLoginButton(){
            
            const account = this.passwordLogin.account
            const password = this.passwordLogin.password
            const loginData = 'account='+account+'&'+'password='+password
            if(this.passwordLogin.account == '' || this.passwordLogin.password.length < 6){
                this.promptMsg = '请填写好信息'
                this.promptShow = true
                setTimeout(()=>{
                    this.promptShow = false
                },2000)
            }else{
                this.loading = true 
                this.$http.post('http://10.8.236.179:8081/api/login',
                loginData).then(
                    response=>{
                        this.loading = false
                        if(response.data.data == '' || response.data.data == []){
                            this.promptMsg = '账号或密码不正确'
                            this.promptShow = true
                            setTimeout(()=>{
                                this.promptShow = false
                            },2000)
                        }else{
                            this.promptMsg = '登录成功'
                            this.promptShow = true
                            this.$bus.$emit('loginStatus',response.data.data)
                            setTimeout(()=>{
                                this.promptShow = false
                                this.$router.back()
                            },500)
                        }
                    },
                    error=>{
                        this.loading = false
                        console.log(error.message)
                    }
                )
            }
        },
        //点击打开注册的界面
        registerAccount(){
            this.$router.push('/login/register')
        },
        //点击打开找回密码界面
        retrieve(){
            this.$router.push('/login/retrieve')
        },
        //点击打开阿炳协议面板
        checkAgreementBook(){
            this.$router.push('/login/agreement')
        }
    },
    watch : {
        'phoneLogin.phoneNumber' : {
            handler(){
                if(this.phoneLogin.phoneNumber.length == 11){
                    this.loginPhone = 'login-letter-button active'
                }else{
                    this.loginPhone = 'login-letter-button'
                }
            }
        },
        'passwordLogin.account':{
            handler(){
                if(this.passwordLogin.account.length != 0){
                    this.loginAccount = 'login-userButton active'
                }else{
                    this.loginAccount = 'login-userButton'
                }
            }
        }
    }
}
</script>

<style>
.login{
    position: fixed;top: 0;left: 0;
    width: 100%;
    height: 100vh;
    background: white;
    z-index:1;
    animation: show .4s ease-out 1;
}
.login-back{
    position: absolute;left: 4vw;top: 4vw;
    width: 6vw;
    height: 6vw;
    text-align: center;
    line-height: 6vw;
    font-size: 6vw;
    color: rgb(175, 175, 175);
}
.login-logo{
    position: absolute;top: 8vw;left: 50%;
    transform: translateX(-50%);
    width: 60%;
    height: 25vw;
    background: url('../../assets/logo.jpg')no-repeat;
    background-size: 100%;
}
.login-head{
    position: absolute;top : 33vw ;left: 50%;
    transform: translateX(-50%);
    width: 60%;
    height: 8vw;
    min-height: 8vw;
    background: white;
}
.login-head ul li{
    float: left;
    width: 50%;
    height: 8vw;
    text-align: center;
    line-height: 8vw;
    color: rgb(0, 176, 199);
}
.toggleSlide{
    position: absolute;left: 0;top: 0;
    width: 50%;
    height: 8vw;
    border-bottom: 3px solid rgb(0, 176, 199);
    transition: all .4s;
}
.toggleSlide.move{
    left: 50%;
}
.login-letter{
    position: absolute;top: 45vw;left: 50%;
    transform: translateX(-50%);
    width: 80%;
    height: 35vw;
}
.login-letter-head{
    width: 100%;
    height: 10vw;
    border-bottom: 1px solid rgb(0, 176, 199);
}
.login-letter-headLeft{
    position: absolute; top: 0; left: 0;
    width: 10vw;
    height: 10vw;
    text-align: center;
    line-height: 10vw;
    color: rgb(126, 126, 126);
    background: rgba(255, 255, 255, 0);
    font-weight: bolder;
}
.login-letter-head input{
    position: absolute; top: 0; left: 10vw;
    width: 60%;
    height: 10vw;
    background: rgba(255, 255, 255, 0);
}
.login-letter-headGet{
    position: absolute; right: 0; top: 0;
    width: 30%;
    height: 10vw;
    text-align: center;
    line-height: 10vw;
    color: rgb(0, 176, 199);
    background: rgba(255, 255, 255, 0);
}
.login-letter-idcode{
    position: absolute; left: 0; top: 10vw;
    width: 100%;
    height: 10vw;
    line-height: 10vw;
    color: rgb(126, 126, 126);
    border-bottom: 1px solid rgb(0, 176, 199);
}
.login-letter-idcode-left{
    float: left;
    margin-top: 1vw;
    text-align: center;
    width: 30vw;
    height: 8vw;
    /* line-height: 8vw; */
}
.login-letter-idcode input{
    float: left;
    margin-left: 2vw;
    width: 40%;
    height: 10vw;
    background: rgba(255, 255, 255, 0);
}
.login-letter-button,.login-userButton{
    position: absolute; left: 0; top: 25vw;
    width: 100%;
    height: 10vw;
    text-align: center;
    line-height: 10vw;
    color: white;
    background: rgb(130, 176, 197);
}
.login-letter-button.active, .login-userButton.active{
    background: rgb(27, 183, 255);
}
.login-account{
    position: absolute;top: 45vw;left: 50%;
    transform: translateX(-50%);
    width: 80%;
    height: 35vw;
}
.login-userAccount,.login-userPassword{
    width: 100%;
    height: 10vw;
    border-bottom:1px solid rgb(0, 176, 199);
}
.login-userAccount input,.login-userPassword input{
    width: 90%;
    height: 10vw;
    font-size: 15px;
    color: rgb(124, 124, 124);
    background: rgba(0, 0, 0, 0);
}
.login-userPassword span{
    color: rgb(124, 124, 124);
    font-size: 17px;
}
.login-userButton{
    bottom: -2vh;
}
.login-userRegister,.login-userForget{
    position: absolute;left: 0;top: 38vw;
    width: 100px;
    height: 20px;
    text-align: center;
    line-height: 20px;
    color: rgb(124, 124, 124);
}
.login-userForget{
    left: 100%;
    margin-left: -100px;
    color: rgb(255, 173, 173);
}
.login-more{
    position: absolute;left: 0;bottom: 10vh;
    width: 100%;
    height: 12vh;
    /* background: red; */
    border-top: 1px solid rgb(204, 204, 204);
}
.login-more::before{
    position: absolute;left: 50%;top: -10px;
    display: inline-block;
    content: '其他登录方式';
    margin-left: -50px;
    width: 100px;
    height: 20px;
    text-align: center;
    color: rgb(204, 204, 204);
    background: white;
}
.login-more-mode:nth-of-type(1),.login-more-mode:nth-of-type(2),.login-more-mode:nth-of-type(3){
    position: absolute;left: 50%;top : 50%;
    transform : translate(-50%,-50%);
    width: 7vh;
    height: 7vh;
    background: url('../../assets/微信图标.jpg') no-repeat center;
    background-size: 80%;
    border-radius: 50%;
    border: 1px solid rgb(204, 204, 204);
}
.login-more-mode:nth-of-type(1){
    left: 25%;
    background: url('../../assets/QQ图标.jpg') no-repeat center;
    background-size: 60%;
}
.login-more-mode:nth-of-type(3){
    left: 75%;
    background: url('../../assets/微博图标.jpg') no-repeat center;
    background-size: 110%;
}
.login-agreement{
    position: absolute;left: 0;bottom: 0;
    width: 100%;
    height: 5vh;
    line-height: 5vh;
    text-align: center;
    color: rgb(105, 105, 105);
}
.login-agreement a{
    color: skyblue;
}

.register-enter,.register-leave-to{
    transform: translateX(-100vw);
}
.register-enter-to,.register-leave{
    transform: translateX(0);
}
@keyframes show {
    from{
        /* left: -100%; */
        top:-100vh
    }
    to{
        /* left: 0; */
        top: 0;
    }
}
@media screen and (max-height : 500px) {
    body {
        overflow: hidden;
    }
    .login-more{
        bottom: -50vw;
    }
}
</style>