<template>

  <div class="setConfig">
      <div class="login-back" @click='$router.back()'></div>
      <div class="user">
        <ul>
          <li>用户昵称
            <span>{{userMsg.nickname}}</span>
          </li>
          <li>真实姓名
            <span>{{userMsg.real_name}}</span>
          </li>
          <li>邮箱
            <span>{{userMsg.email}}</span>
          </li>
          <li>手机号
            <span>{{userMsg.phone}}</span>
          </li>
          <li>年龄
            <span>{{userMsg.age}}</span>
          </li>
          <li>性别
            <span>{{userMsg.gender}}</span>
          </li>
        </ul>
      </div>
      <div class="reviseUserMsg" @click='reviseUserMsg'>修改用户信息</div>
      <div class="exitLogin" ref="exitLogin" @click='exit'>{{userMsg.status}}</div>
      <transition>
        <router-view
        :user='user'
        />
      </transition>
  </div>

</template>

<script>
export default {
    name : 'SetConfig',
    props:['user'],
    data(){
      return {
        userMsg : {
          status : '用户未登录',
          nickname : '未登录',
          phone : '未登录',
          real_name : '未登录',
          email :'未登录',
          age : '未登录',
          gender : '未登录'
        }
      }
    },
    methods:{
      reviseUserMsg(){
        if(this.user.status == false){
          alert('请先登录')
        }else{
          this.$router.push('/individual/set/revise_user')
        }
      },
      //退出当前账号
      exit(){
        if(this.user.status == true){
          this.$bus.$emit('exitLogin')
          this.$router.back()
        }else{
          alert('你还未登录任何账号')
        }
        
      }
    },
    mounted(){
      if(this.user.status == true){
        this.$refs.exitLogin.classList.add('current')
        this.userMsg.status = '退出当前账户'
        this.userMsg.nickname = this.user.nickname
        this.userMsg.phone = this.user.account
        this.userMsg.real_name = this.user.real_name
        this.userMsg.email = this.user.email
        this.userMsg.age = this.user.age
        this.userMsg.gender=  this.user.gender
      }
    }
}
</script>

<style lang='less'>
.setConfig{
  position: absolute; top: 0; left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(255, 255, 255);
  z-index: 2;
  transition: all .3s ease-out;
  .user{
    position: absolute; top: 15vw; left: 5vw;
    width: 90vw;
    height: 70vw;
    li{
      position: relative;
      margin: 4vw 0 0 0 ;
      padding: 0 3vw;
      box-sizing: border-box;
      width: 84vw;
      height: 10vw;
      line-height: 10vw;
      color: rgb(92, 92, 92);
      box-shadow: 0 0 2px rgb(180, 180, 180);
      span{
        display: inline-block;
        position: absolute; right: 5vw;
      }
    }
  }
  .exitLogin,.reviseUserMsg{
  position: absolute; left: 5vw; top: 140vw;
  width: 90vw;
  height: 10vw;
  text-align: center;
  line-height: 10vw;
  color: white;
  z-index: 4;
  background: rgb(145, 145, 145);
  border-radius: 3px;
  }
  .reviseUserMsg{
    top:125vw;
    background: rgb(86, 193, 255);
  }
  .exitLogin.current{
      background: red;
  }
}
v-enter,v-leave-to{
    transform: translateY(-100vh);
}
v-enter-to,v-leave{
    transform: translateY(0);
}



</style>