<template>
  
  <div class="register">
      <div class="login-back" @click='$router.back()'></div>
      <form class="registerPanel" @submit.prevent>
          <div class="register-nickname">
              <div class="input-header">昵&nbsp;&nbsp;&nbsp;&nbsp;称:</div>
              <input type="text" v-model='registerData.nickname'>
          </div>
          <div class="register-phoneNumber">
              <div class="input-header">邮&nbsp;&nbsp;&nbsp;&nbsp;箱:</div>
              <input type="text" v-model='registerData.email'>
          </div>
          <div class="register-email">
              <div class="input-header">手&nbsp;机&nbsp;号:</div>
              <input type="text" v-model='registerData.phoneNumber' @blur='checkPhoneNumber'>
          </div>
          <div class="register-password">
              <div class="input-header">密&nbsp;&nbsp;&nbsp;&nbsp;码:</div>
              <input type="password" v-model='registerData.password'>
              <span @click='showPassword("open")' v-show='!passwordShow'></span>
              <span @click='showPassword("close")' v-show='passwordShow' style='color:skyblue'></span>
          </div>
          <div class="register-repassword">
              <div class="input-header">确认密码:</div>
              <input type="password" v-model='registerData.repassword'>
              <span @click='showRePassword("open")' v-show='!repasswordShow'></span>
              <span @click='showRePassword("close")' v-show='repasswordShow' style='color:skyblue'></span>
          </div>
          <button @click='register'>注册</button>
          <div class="register-promise">*我承诺对所有用户密码进行加密*</div>
      </form>
      <!-- 提示弹窗 -->
      <div class="registerPrompt" v-show='promptShow'><span style='color:red'></span>&nbsp;{{promptMsg}}</div>
      <!-- 加载图标 -->
      <div class="loading" v-show='loading'>
            <div class="loadingSvg"></div>
      </div>
  </div>
</template>

<script>
export default {
    name : 'Register',
    data(){
        return {    
            registerData : {
                nickname : '',
                phoneNumber : '',
                email : '',
                password : '',
                repassword : ''
            },
            promptShow : false,//提示弹窗显示状态
            promptMsg : '',//提示弹窗内容

            passwordShow : false,
            repasswordShow : false,

            loading:false
        }
    },
    methods : {
        //控制密码明文或者密文状态
        showPassword(params){
            const password  =document.querySelector('.register-password input')
            if(params == 'open'){
                this.passwordShow = true
                password.type = 'text'
            }else{
                this.passwordShow = false
                password.type = 'password'
            }
        },
        //控制重复密码明文或者密文状态
        showRePassword(params){
            const rpw  =document.querySelector('.register-repassword input')
            if(params == 'open'){
                this.repasswordShow = true
                rpw.type = 'text'
            }else{
                this.repasswordShow = false
                rpw.type = 'password'
            }
        },
        //检查用户手机号输入格式是否正确
        checkPhoneNumber(){
            if(this.registerData.phoneNumber.length != 11){
                this.promptShow = true
                this.promptMsg = '手机号格式不正确'
                setTimeout(()=>{
                    this.promptShow = false
                },3000)
            }
        },
        //注册按钮
        register(){
            const rpw  = document.querySelector('.register-repassword input')
            if(this.registerData.password != this.registerData.repassword){
                this.promptShow = true
                this.promptMsg = '两次密码不一致'
                rpw.focus()
                setTimeout(()=>{
                    this.promptShow = false
                },3000)
            }else if(this.registerData.password == '' ||  this.registerData.repassword == ''&&
            this.registerData.nickname == '' && this.registerData.email == '' && this.registerData.phoneNumber==''){
                this.promptShow = true
                this.promptMsg = '有信息未填'
                setTimeout(()=>{
                    this.promptShow = false
                },3000)
            }else{
                this.loading = true
                const registerMsg = 'account=' + this.registerData.phoneNumber + '&' +'password=' + this.registerData.password + '&'
                + 'status=false' + '&' + 'email=' + this.registerData.email +'&' + 'nickname=' + this.registerData.nickname + '&' + 'coin=' + 0
                this.$http.post('http://10.8.236.179:8081/api/register',registerMsg).then(
                    response=>{
                        this.loading = false
                        if(response.data == '该号码已被注册'){
                            this.promptShow = true
                            this.promptMsg = '该号码已被注册'
                            setTimeout(()=>{
                                this.promptShow = false
                            },3000)
                        }else {
                            this.promptShow = true
                            this.promptMsg = '注册成功'
                            setTimeout(()=>{
                                this.promptShow = false
                                this.$router.back()
                            },500)    
                            this.registerData.phoneNumber = '';this.registerData.password = '';this.registerData.repassword = '';
                            this.registerData.nickname ='' ;this.registerData.email = ''
                        }
                    },
                    error=>{
                        this.loading = false
                        console.log(error.message)
                    }
                )
            }
        }
    }
}
</script>

<style>
.register{
    position: absolute ; top: 0; left: 0;
    width: 100vw;
    height: 100vh;
    min-height: 1000vw;
    overflow: hidden;
    background: white;
    transition: all .3s ease-out;
    z-index: 2;
}
.registerPrompt{
    position: absolute; left: 50%; top : 0;
    margin-left: -25vw;
    width: 50vw;
    height: 10vw;
    text-align: center;
    line-height: 15vw;
    animation: registerPrompt .3s linear 1;
    /* background: rgb(226, 226, 226); */
}
.registerPanel{
    position: absolute; left: 10vw; top : 15vw;
    width: 80vw;
    height: 80vw;
    background: white;
}
.register-nickname,
.register-phoneNumber,
.register-email,
.register-password,
.register-repassword,
.register-promise{
    position: relative;
    width: 100%;
    height: 10vw;
    border-bottom:1px solid skyblue ;
}
.registerPanel button{
    position: absolute; top: 60vw; left: 0;
    width: 100%;
    height: 10vw;
    text-align: center;
    line-height: 10vw;
    color: white;
    background: skyblue;
}
.input-header{
    display: inline-block;
    width: 20vw;
    height: 10vw;
    text-align: right;
    line-height: 10vw;
    font-weight: bold;
    color:rgb(93, 163, 255);
}
.register-nickname input,
.register-phoneNumber input,
.register-email input,
.register-password input, 
.register-repassword input{
    position: absolute; top: 3.8vw; left: 22vw;
    width: 60vw;
    height: 5vw;
    font-weight: bold;
    color: rgb(170, 170, 170);
}
.register-password span,
.register-repassword span{
    display: inline-block;
    position: absolute; right: 2vw; top: 1vw;
    width: 8vw;
    height: 8vw;
    text-align: center;
    line-height: 8vw;
    color: rgb(141, 141, 141);
}
.register-promise{
    position: absolute; top: 70vw; left: 0;
    width: 100%;
    text-align: center;
    line-height: 10vw;
    color: rgb(205, 146, 90);
    border-bottom: transparent;
}
@keyframes registerPrompt {
   0%{
      transform: translateY(-10vw);
   }
   100%{
      transform: translateY(0);
   }
}
</style>