<template>
  <div class="get_likes">
      <div class="login-back" @click='$router.back()'></div>
      <div class="middle_heart"></div>
      <div class="heart_number">你一共收到了:{{number}}个赞</div>
  </div>
</template>

<script>
export default {
    name :'GetLikes',
    props :['getLikesNumber'],
    data(){
        return {
            number : 0
        }
    },
    mounted(){
        this.getLikesNumber.forEach(like => {
            this.number += parseInt(like.article_like)
        });
    }
    
}
</script>

<style lang='less'>
.get_likes{
    position: absolute; top:0;left: 0;
    width: 100vw;
    height: 100vh;
    background: white;
    transition: all .3s ease-out;
    .middle_heart{
        position: absolute; left: 50%; top : 30%;
        margin: -40px 0 0 -40px;
        width: 80px;
        height: 80px;
        text-align: center;
        line-height: 80px;
        font-size: 80px;
        color: skyblue;
        animation: heartChange 3s linear infinite;
    }
    .heart_number{
        position: absolute; left: 50%; top : 45%;
        transform : translate(-50%,-50%);
        width: 300px;
        height: 20px;
        color:skyblue;
        text-align: center;
        line-height: 20px;
        font-size: 20px;
    }
}
@keyframes heartChange {
    0%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(20px);
    }
    100%{
        transform: translateY(0);
    }
}
</style>