<template>
  <div class="retrieve">
      <div class="login-back" @click='$router.back()'></div>
      <div class="retrieve-title">

      </div>
      <div class="retrieve-body">
          <header>请输入你要找回账号所绑定的手机号</header>
              <input type="text" v-model='newPassword'>
          <button @click='searchAccount'>查询</button>
      </div>
      <transition>
      <router-view
        :getId='getId'
      />
      </transition>
  </div>
</template>

<script>
export default {
    name : 'Retrieve',
    data(){
        return{
            newPassword : '',

            getId : '',

            isShowRevisePassword : false
        }
    },
    methods:{   
        searchAccount(){
            let password = 'account=' + this.newPassword
            this.$http.post('http://10.8.236.179:8000/api/searchAccount',password).then(
                response=>{
                    if(response.data == '没有结果'){
                        alert('当前账号不存在')
                    }else{
                        this.getId = response.data[0].id
                        console.log(this.getId)
                        this.$router.push('/login/retrieve/revise_password')
                    }
                },
                error=>{
                    console.log(error.message)
                }
            )
        }
    }
}
</script>

<style>
.retrieve{
    position: absolute; top: 0; left: 0;
    width: 100vw;
    height: 100vh;
    min-height: 1000vw;
    overflow: hidden;
    z-index: 2;
    background: white;
    transition: all .3s ease-out;
}
.retrieve-title{
    position: absolute; top: 10vw; left: 15vw;
    width: 70vw;
    height: 24vw;
    background: url('../../assets/logo.jpg')no-repeat;
    background-size: 100%;
}
.retrieve-body{
    position: absolute; left: 50%; top : 35vw;
    margin-left: -40vw;
    width: 80vw;
    height: 80vw;
    /* background: red; */
}
.retrieve-body header{
    position: absolute; top: 0; left: 50%;
    margin-left: -45vw;
    width: 90vw;
    height: 14vw;
    font-size: 16px;
    text-align: center;
    line-height: 14vw;
    color: skyblue;
    border-bottom: 1px solid skyblue;
}
.retrieve-body input{
    position: absolute; top: 20vw; left: 15vw;
    width: 50vw;
    height: 8vw;
    border: 1px solid skyblue;
}
.retrieve-body button{
    position: absolute; top: 35vw; left: 15vw;
    width: 50vw;
    height: 8vw;
    color: white;
    background: skyblue;
}

v-enter,v-leave-to{
    transform: translateY(-100vh);
}
v-enter-to,v-leave{
    transform: translateY(0);
}
</style>