//引入路由
import VueRouter from "vue-router";

//引入路由组件
import Home from '../pages/Home/Home'
import FilterEntries from '../pages/Home/FilterEntries'

import Search from "../pages/Search/Search"

import Community from "../pages/Community/Community"
import ComRecommend from "../pages/Community/ComRecommend"
import ComNew from "../pages/Community/ComNew"

import Individual from "../pages/Individual/Individual"
import SetConfig from '../pages/Individual/SetConfig'
import MyArticle from '../pages/Individual/MyArticle'
import Vip from '../pages/Individual/Vip'
import GetLikes from '../pages/Individual/GetLikes'
import ReviseUserMsg from '../pages/Individual/ReviseUserMsg'
//引入登入组件
import Login from "../pages/Login/Login"
import Register from '../pages/Login/Register'
import Retrieve from '../pages/Login/Retrieve'
import Agreement from '../pages/Login/Agreement'
import RevisePassword from '../pages/Login/RevisePassword'
//引入二级路由组件

export default new VueRouter({
    routes : [
        {path:'/' , redirect : '/home'},
        {
            path : '/login',
            component : Login,
            children : [
                {
                    path : '/login/register',
                    component : Register
                },
                {
                    path : '/login/retrieve',
                    component : Retrieve,
                    children : [
                        {
                            path : '/login/retrieve/revise_password',
                            component : RevisePassword
                        }
                    ]
                },
                {
                    path : '/login/agreement',
                    component : Agreement
                }
            ]
        },
        {
            path : '/home',
            // redirect : '/home',
            component : Home,
            children : [
                {
                    path : '/filer_entries',
                    component : FilterEntries
                }
            ]
        },
        {
            path : '/search',
            component : Search
        },
        {
            path : '/community',
            component : Community,
            children : [
                {
                    path : '/community/recommend',
                    component : ComRecommend
                },
                {
                    path : '/community/new',
                    component : ComNew
                }
            ]
        },
        {
            path : '/individual',
            component : Individual,
            children : [
                {
                    name : 'set_config',
                    path : '/individual/set',
                    component : SetConfig,
                    children : [
                        {
                            path : '/individual/set/revise_user',
                            component : ReviseUserMsg
                        }
                    ]
                },
                {
                    path : '/individual/my_article',
                    component : MyArticle
                },
                {
                    path : '/individual/vip',
                    component : Vip
                },
                {
                    path:'/individual/get_likes',
                    component : GetLikes
                }
            ]
        }
    ]
})