<template>
  <div class="comnew">
    <ul>
        <li v-for="a in articles" :key='a.article_id' @click='checkArticleInfo(a.article_id)'>
          <div class="articleTitle">{{a.article_title}}</div>
          <div class="articleAuthor">
            作者:{{a.user_nickname}}
            </div>
          <div class="articleLikes">{{a.article_like}}点赞</div>
        </li>
    </ul>
    <!-- 加载面板 -->
    <div class="articlesIsLoading" v-show='isloading'>
        <span></span>
        <h5>加载中...</h5>
    </div>
  </div>
</template>

<script>
export default {
    name : 'ComNew',
    data(){
      return {
        articles : [],
        isloading : false,
      }
    },
    methods:{
      checkArticleInfo(id){
        this.$bus.$emit('checkArticleInfo',id)
      }
    },
    mounted(){
      this.isloading = true
      this.$http.get('http://10.8.236.179:8081/api/getArticle').then(
        response=>{
          this.isloading = false
          this.articles = response.data
          this.articles.sort((before,after)=>{
              return after.publish_date - before.publish_date
          })
          console.log(response.data)
        },
        error=>{
          this.isloading = false
          console.log(error.message)
        }
      )
    }
}
</script>

<style>
.comnew{
    position: absolute; top : 29vw ; left: 0;
    width: 100%;
    height: 73vh;
    overflow-y: scroll;
}
.comnew ul{
  width: 100vw;
}
.comnew ul li{
  position: relative;
  width: 100%;
  height: 30vw;
  border-bottom: 1px solid rgb(206, 206, 206);
}

</style>