<template>
  <div>
    <router-view 
      :films='films'
      :user = 'user'
    />
    <FooterGuide/>
    <div class="loading" v-show='loading'>
          <div class="loadingSvg"></div>
    </div>
    <!-- 封面 -->
    <div class="postPanel" v-show="postShow">
      <div class="post_A">A</div>
      <div class="post_B">B</div>
      <div class="post_logo">影视</div>
      <button @click='postShow = false'>跳过{{post_time}}s</button>
    </div>
  </div>
</template>

<script>
import FooterGuide from './components/FooterGuide/FooterGuide.vue'
export default {
  name : 'App',
  components : {
    FooterGuide
  },
  data(){
    return {
      films : '',
      user : {
        nickname : '请先登录',
        status : false,
        coin : 0,
        account : '',//用户手机号
        fans : '',
        headUrl : '9d589f239e4d11d86734540db915e242播放按钮光明.png',
        real_name :'',
        email :'',
        age :'',
        gender : ''
      },
      loading : false,
      postShow : true,
      post_time : 5
    }
  },
  methods :{
    //登录成功后的函数
    loginStatus(user){
      this.user.nickname = user[0].nickname
      this.user.status = true
      this.user.coin = user[0].coin
      this.user.account = user[0].account
      this.user.fans = user[0].fans
      this.user.headUrl = user[0].head_image
      this.user.real_name = user[0].real_name
      this.user.email = user[0].email
      this.user.age = user[0].age
      this.user.gender = user[0].gender
    },
    //筛选电影
    filterSortByChose(filterObj){
        console.log(filterObj.region)
        
        if(filterObj.region != '' || filterObj.time != '' || filterObj.type != ''){
          if(filterObj.region != ''){
              this.films = this.films.filter((f)=>{
                  return f.film_region == filterObj.region
              })
              if(filterObj.type != ''){
                this.films = this.films.filter((f)=>{
                  return f.film_type == filterObj.type
                })
                if(filterObj.time != ''){
                  if(filterObj.time == 0){
                    this.films.sort((before,after)=>{
                      return after.film_date - before.film_date
                    })
                  }else{
                    this.films.sort((before,after)=>{
                      return before.film_date - after.film_date
                    })
                  }
                }
              }else{
                if(filterObj.time != ''){
                  if(filterObj.time == 0){
                    this.films.sort((before,after)=>{
                      return after.film_date - before.film_date
                    })
                  }else{
                    this.films.sort((before,after)=>{
                      return before.film_date - after.film_date
                    })
                  }
                }
              }
          }else{
            if(filterObj.type != ''){
                this.films = this.films.filter((f)=>{
                  return f.film_type == filterObj.type
                })
                if(filterObj.time != ''){
                  if(filterObj.time == 0){
                    this.films.sort((before,after)=>{
                      return after.film_date - before.film_date
                    })
                  }else{
                    this.films.sort((before,after)=>{
                      return before.film_date - after.film_date
                    })
                  }
                }
              }else{
                if(filterObj.time != ''){
                  if(filterObj.time == 1){
                    console.log(filterObj.time)
                    this.films.sort((before,after)=>{
                      return after.film_date - before.film_date
                    })
                  }else{
                    this.films.sort((before,after)=>{
                      return before.film_date - after.film_date
                    })
                  }
                }
              }
          }
          console.log(123)
        }
    },
    updataDataBase(){
      this.$http.get('http://localhost:8000').then(
          response=>{
            this.films = response.data.data
            this.films.sort((before,after)=>{
              return after.id - before.id
            })
          },
          error=>{
            console.log('请求失败',error.message)
          },
      )
    },
    //退出当前登录账号
    exitLogin(){
      this.user.status = false
      this.user.nickname = '请先登录'
    },
    //修改头像成功后立即更换头像
    reviseUserHead(url){
      this.user.headUrl = url.head_image
    }
  },
  mounted(){
    //5秒钟封面海报自动关闭
    setTimeout(()=>{
      this.postShow = false
    },5000)
    setInterval(() => {
      this.post_time = this.post_time -1
    }, 1000);
    this.loading = true
    this.$http.get('http://localhost:8000').then(
          response=>{
            this.loading = false
            this.films = response.data.data
            this.films.sort((before,after)=>{
              return after.id - before.id
            })
          },
          error=>{
            this.loading = false
            console.log('请求失败',error.message)
          },
    )
    this.$bus.$on('loginStatus',this.loginStatus)
    this.$bus.$on('filterSortByChose',this.filterSortByChose)
    this.$bus.$on('updataDataBase',this.updataDataBase)
    //退出当前登录账号事件总站
    this.$bus.$on('exitLogin',this.exitLogin)
    //修改头像成功后立即更换头像
    this.$bus.$on('reviseUserHead',this.reviseUserHead)
  }
}
</script>

<style>
.postPanel{
  position: absolute; top: 0; left: 0;
  width: 100vw;
  height: 100vh;
  /* background: url('assets/封面.png')no-repeat; */
  /* background-size: cover; */
  background: skyblue;
  z-index:10;
}
.postPanel button{
  position: absolute; right: 5vw; top: 5vw;
  width: 15vw;
  height: 15vw;
  text-align: center;
  line-height: 15vw;
  background: white;
  border-radius: 50%;
}
.post_logo{
  position: absolute; left: 65vw; top : 50%;
  transform : translate(-50%,-50%);
  width: 60vw;
  height: 20vw;
  line-height: 20vw;
  text-align: center;
  color: white;
  font-size: 8vw;
}
.post_A,.post_B{
  position: absolute; left: 25vw; top : 50%;
  margin-top:-16vw;
  width: 20vw;
  height: 20vw;
  font-size: 20vw;
  line-height: 20vw;
  text-shadow: 3px 3px 2px black;
  color: red;
  animation: wordMove 1.8s linear infinite;
}
.post_B{
  left: 40vw;
  animation-delay: .16s;
}
/* 加载面板 */
.loading{
  position: absolute; left: 50%; top : 50%;
  transform : translate(-50%,-50%);
  width: 15vh;
  height: 15vh;
  background: rgb(240, 240, 240 , 0.6);
  border-radius: 2vh;
}
.loadingSvg{
  position: absolute; left: 50%; top : 50%;
  margin: -4vh 0 0 -4vh;
  width: 8vh;
  height: 8vh;
  color: skyblue;
  font-size: 6vh;
  text-align: center;
  line-height: 8vh;
  animation: loadingRunning 3s linear infinite;
}
/* 登录页面弹窗 */
.loginPrompt{
  position: absolute; top: 65vh; left: 50%;
  transform: translate(-50%,0);
  width: 20vh;
  height: 8vh;
  text-align: center;
  line-height: 8vh;
  background: rgb(241, 241, 241);
  border-radius: 4vh;
  animation: promptShow .3s linear 1;
}
@keyframes promptShow {
   0%{
      transform: translate(-50%,30%);
      opacity: 0;
   }
   100%{
     transform: translate(-50%,0);
      opacity: 1;
   }
}
@keyframes loadingRunning {
   0%{
      transform: rotate(0);
   }
   100%{
      transform: rotate(360deg);
   }
}
@keyframes wordMove {
   0%{
      transform: translateY(0);
   }
   15%{
     transform: translateY(-6vw);
   }
   25%{
      transform: translateY(0);
   }
   100%{
     transform: translateY(0);
   }
}
</style>