<template>
  <div class="comRecommend">
      <ul>
        <li v-for="a in articles" :key='a.article_id' @click='checkArticleInfo(a.article_id)'>
          <div class="articleTitle">{{a.article_title}}</div>
          <div class="articleAuthor">
            作者:{{a.user_nickname}}
            </div>
          <div class="articleLikes">{{a.article_like}}点赞</div>
        </li>
      </ul>
      <!-- 加载面板 -->
      <div class="articlesIsLoading" v-show='isloading'>
        <span></span>
        <h5>加载中...</h5>
      </div>
      
  </div>
</template>

<script>
export default {
    name : 'ComRecommend',
    data(){
      return {
        articles : [],
        isloading : false,
      }
    },
    methods:{
      checkArticleInfo(id){
        this.$bus.$emit('checkArticleInfo',id)
      }
    },
    mounted(){
      this.isloading = true
      this.$http.get('http://10.8.236.179:8081/api/getArticle').then(
        response=>{
          this.isloading = false
          this.articles = response.data
          this.articles.sort((before,after)=>{
            return after.article_like - before.article_like
          })
        },
        error=>{
          this.isloading = false
          console.log(error.message)
        }
      )
    }
}
</script>

<style>
.comRecommend{
    position: absolute; top : 29vw ; left: 0;
    width: 100%;
    height: 73vh;
    overflow-y: scroll;
}
.comRecommend ul{
  width: 100vw;
}
.comRecommend ul li{
  position: relative;
  width: 100%;
  height: 30vw;
  border-bottom: 1px solid rgb(206, 206, 206);
}
.articleTitle{
  position: absolute; left: 5vw;top: 2vw;
  width: 90vw;
  height: 10vw;
  line-height: 10vw;
  text-align: left;
  font-size: 18px;
  font-weight: bolder;
  color: rgb(87, 87, 87);
}
.articleLikes{
  position: absolute; right: 5vw; bottom: 0;
  width: 30vw;
  height: 6vw;
  text-align: right;
  line-height: 6vw;
  color: rgb(141, 141, 141);
}
.articleAuthor{
  position: absolute; left: 5vw; bottom: 2vw;
  width: 50vw;
  height: 7vw;
  line-height: 7vw;
  color: rgb(141, 141, 141);
}
.articlesIsLoading{
  position: absolute; left: 50%; top : 40%;
  transform : translate(-50%,-50%);
  width: 30vw;
  height: 30vw;
  background: rgb(241, 241, 241);
  border-radius: 5vw;
}
.articlesIsLoading span{
  position: absolute; left: 50%; top : 50%;
  margin:-7vw 0 0 -5vw;
  display: inline-block;
  width: 10vw;
  height: 10vw;
  text-align: center;
  line-height: 10vw;
  font-size: 10vw;
  color: rgb(25, 221, 255);
  animation: loading 2.5s linear infinite;
}
.articlesIsLoading h5{
  position: absolute; bottom: 4vw;
  width: 30vw;
  color: rgb(25, 221, 255);
  text-align: center;
}
@keyframes loading {
   0%{
      transform: rotate(0);
   }
   100%{
      transform: rotate(360deg);
   }
}
</style>