//该文件用于创建Vuex中最核心的store
import Vue from 'vue'
//引入
import Vuex from 'vuex'

//使用插件
Vue.use(Vuex)
// Vue.use(Vuex)
//准备一个actions用于响应组件的动作
const actions = {}
//准备mutations用于操作数据
const mutations = {}
//准备state用于储存数据
const state = {}


export default new Vuex.Store({
    actions,
    mutations,
    state
})