<template>
  <div>
    <div class="search-head">搜 索</div>
    <div class="search-search">
      <span>输入你要搜索的电影</span>
      <input type="text" v-model="searchInfo">
      <button @click='searchFilm'></button>
    </div>
    <div class="search-result">
      <ul>
        <li v-for="sf in searchResult" :key='sf.id' @click='checkDetails(sf.id)'>
          <div class="search-result-post">
            <img :src="sf.film_post" alt="">
          </div>
           <header>{{sf.film_name}}</header>
           <footer>主演:{{sf.film_actor}}</footer>
           <main>{{sf.film_score}}</main>
        </li>
      </ul>
    </div>
      

    <!-- 电影详情页 -->
    <div class="home-filmDetails" v-show="showFilmDetails">
      <div class="hone-closeFilmDetails" @click='showFilmDetails = !showFilmDetails'></div>
      <div class="home-filmDetails-post">
        <img :src="searchResultDetails.film_post" alt="" style='width:100%;height:100%'>
      </div>
      <div class="home-filmDetails-name">
          {{searchResultDetails.film_name}}
      </div><header>BD</header>
      <div class="home-filmDetails-time">
          发行年份：{{searchResultDetails.film_date}}
      </div>
      <div class="home-filmDetails-actors">
        主演：<a>{{searchResultDetails.film_actor}}</a>
      </div>
      <div class="home-filmDetails-director">
        导演：{{searchResultDetails.film_director}}
      </div>
      <div class="home-filmDetails-region">
        地区：{{searchResultDetails.film_region}}
      </div>
      <div class="home-filmDetails-score">
        {{searchResultDetails.film_score}}% <span style='color:red'></span>
      </div>
      <div class="home-filmDetails-message">
        <span>剧情详情：</span><br>&nbsp;&nbsp;&nbsp;
        <span style="color:rgb(0, 90, 100)">{{searchResultDetails.film_message}}</span>
      </div>
    </div>

    <!-- 搜索出错 -->
    <div class="search-mistake" v-show='showMistake'></div>
  </div>
</template>

<script>
export default {
    name : 'Search',
    props:['films'],
    data(){
      return{
        searchInfo : '',
        searchResult : [],
        searchResultDetails : [],

        showFilmDetails : false,

        showMistake : false
      }
    },
    methods:{
      searchFilm(){
        if(this.searchInfo == ''){
          this.searchResult = []
        }else{
            this.searchResult = this.films.filter(sf => {
              return sf.film_name.indexOf(this.searchInfo) != -1
            });
            if(this.searchResult == ''){
              this.showMistake = true
                setTimeout(()=>{
                  this.showMistake = false
                },5000)
            }else{
              this.showMistake = false
            }
        }  
        // this.$http.get(`'http://localhost:8081/api/?search=${this.searchInfo}'`)
      },
      checkDetails(id){
        this.searchResult.forEach((f)=>{
          if(f.id == id){
            this.showFilmDetails = true
            this.searchResultDetails = f
          }
        })
      }
    },
    mounted(){
      
    }
}
</script>

<style>
.search-head{
  position: absolute;top: 0;left: 0;
  width: 100%;
  height: 14vw;
  text-align: center;
  line-height: 14vw;
  color: white;
  font-size: 16px;
  background: rgb(25, 182, 255);
}
.search-search{
  position: absolute; top: 14vw; left: 0;
  width: 100%;
  height: 14vw;
  border-bottom: 1px solid rgb(188, 234, 255);
}
.search-search  span{
  position: absolute; left: 0; top: 0;
  width: 40vw;
  height: 14vw;
  line-height: 14vw;
  text-align: center;
}
.search-search input{
  position: absolute; left: 40vw; top : 50%;
  transform : translate(0,-50%);
  width: 40vw;
  height: 8vw;
  border: 1px solid skyblue;
}
.search-search button{
  position: absolute; left: 90%; top : 50%;
  transform : translate(-50%,-50%);
  width: 9vw;
  height: 9vw;
  line-height: 9vw;
  color: rgb(0, 132, 172);
  background: rgb(188, 234, 255);
}
.search-result{
  position: absolute; top: 28vw; left: 0;
  width: 100%;
  height: 74vh;
  overflow-y: scroll;
  overflow-x: hidden;
}
.search-result ul li{
  position: relative;
  box-sizing: border-box;
  width: 100vw;
  height: 100px;
  /* background: rgb(207, 207, 207); */
  border-bottom: 1px solid skyblue;
}
.search-result-post{
  position: absolute; top: 10px; left: 10px;
  width: 60px;
  height: 80px;
}
.search-result-post img{
  width: 100%;
  height: 100%;
}
.search-result ul li header{
  position: absolute; top: 10px; left: 100px;
  width: 50vw;
  height: 30px;
  text-align: left;
  line-height: 30px;
  font-size: 16px;
  font-weight: bolder;
  background: white;
}
.search-result ul li footer{
  position: absolute; top: 50px; left: 100px;
  width: 50vw;
  height: 30px;
  text-align: left;
  line-height: 30px;
  font-size: 8px;
  background: white;
  overflow: hidden;
}
.search-result ul li main{
  position: absolute; right: 33px; top: 33px;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  border-radius: 50%;
  color: white;
  background: skyblue;
}

.search-mistake{
  position: absolute; left: 50%; top : 50%;
  transform : translate(-50%,-50%);
  width: 100px;
  height: 100px;
  background: url('../../assets/遗憾.jpg') no-repeat;
  background-size: cover;
}
.search-mistake:after{
  display: inline-block;
  content: '抱歉，没有找到该资源！';
  position: absolute; bottom: -10px; left: 0;
  width: 100px;
  height: 15px;
  font-weight: bolder;
  text-align: center;
}
</style>