<template>
  <div class="FilterEntries">
      <div class="login-back" @click='backHome'></div>
      <header>选择分类</header>
      <main>
          <div class="timeSort">
              <ul>
                  <li>年份从上到下</li>
                  <li>年份从下到上</li>
              </ul>
          </div>
          <div class="typeSort">
              <ul>
                    <li>科幻魔幻</li>
                    <li>历史战争</li>
                    <li>恐怖惊悚</li>
                    <li>剧情动作</li>
                    <li>动画动漫</li>
              </ul>
          </div>
          <div class="regionSort">
              <ul>
                  <li>欧美</li>
                  <li>亚洲</li>
              </ul>
          </div>
      </main>
  </div>
</template>

<script>
export default {
    name : 'FilterEntries',
    data(){
        return{
            filterSortMsg :{
                time : '',
                type : '',
                region : ''
            }
        }
    },
    methods:{
        backHome(){
            this.$bus.$emit('filterSortByChose',this.filterSortMsg)
            this.$router.back()
        }
    },
    mounted(){
        const time_li = document.querySelectorAll('.FilterEntries .timeSort li')
        for(let i =0 ;i<time_li.length ; i++){
            time_li[i].addEventListener('click',()=>{
                for(let i=0;i<time_li.length;i++){
                    time_li[i].classList.remove('current')
                }
                time_li[i].classList.add('current')
                this.filterSortMsg.time = (i+1)
            })
        }
        const type_li = document.querySelectorAll('.FilterEntries .typeSort li')
        for(let i =0 ;i<type_li.length ; i++){
            type_li[i].addEventListener('click',()=>{
                for(let i=0;i<type_li.length;i++){
                   type_li[i].classList.remove('current')
                }
               type_li[i].classList.add('current')
               if(i==0){
                   this.filterSortMsg.type = '科幻魔幻'
               }else if(i==1){
                   this.filterSortMsg.type = '历史战争'
               }else if(i==2){
                   this.filterSortMsg.type = '恐怖惊悚'
               }else if(i==3){
                   this.filterSortMsg.type = '剧情动作'
               }else{
                   this.filterSortMsg.type = '动画动漫'
               }
            })
        }
        const region_li = document.querySelectorAll('.FilterEntries .regionSort li')
        for(let i =0 ;i<region_li.length ; i++){
            region_li[i].addEventListener('click',()=>{
                for(let i=0;i<region_li.length;i++){
                    region_li[i].classList.remove('current')
                }
                region_li[i].classList.add('current')
                if(i == 0){
                    this.filterSortMsg.region = '美国'
                }else{
                    this.filterSortMsg.region = '中国'
                }
            })
        }
    }
}
</script>

<style>
.FilterEntries{
    position: absolute; top: 0; left: 0;
    width: 100%;
    height: 100vh;
    background: rgb(255, 255, 255);
    z-index: 5;
}
.FilterEntries li{
    box-sizing: border-box;
    float: left;
    margin: 2.5vw 0 0 13.3vw;
    width: 30vw;
    height: 10vw;
    text-align: center;
    line-height: 10vw;
    font-size: 2vw;
    color: skyblue;
}
.FilterEntries li.current{
    font-weight: bolder;
    color: rgb(14, 99, 133);
    text-shadow: 0 0 10px skyblue;
}
.FilterEntries header{
    position: absolute; top: 5vh; left: 50%;
    margin: 0 0 0 -25vw;
    width: 50vw;
    height: 15vw;
    text-align: center;
    line-height: 15vw;
    font-weight: bolder;
    font-size: 5vw;
    color: rgb(31, 166, 255);
}
.FilterEntries main{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    
}
.timeSort ul{
    position: absolute; top: 22vh; left: 50%;
    margin: 0 0 0 -50vw;
    width: 100vw;
    height: 15vw;
}
.typeSort ul{
    position: absolute; top: 22vh; left: 50%;
    margin: 20vw 0 0 -50vw;
    width: 100vw;
    height: 30vw;
}
.typeSort ul li{
    margin: 2.5vw 0 0 2.5vw ;
    width: 30%; 
}
.regionSort ul {
    position: absolute; top: 22vh; left: 50%;
    margin: 55vw 0 0 -50vw;
    width: 100vw;
    height: 15vw;
}
.regionSort ul:after,
.typeSort ul:after,
.timeSort ul:after{
    display: inline-block;
    position: absolute; left: 50%;
    transform : translate(-50%,-50%);
    width: 50vw;
    height: 8vw;
    text-align: center;
    line-height: 8vw;
    color: rgb(15, 133, 180);
    font-weight: bolder;
    font-size: 2.9vw;
    border-bottom: 1px solid rgb(104, 212, 255);
}
.timeSort ul:after{
    content: '选择年份';
}
.typeSort ul:after{
    content: '类型选择';
}
.regionSort ul:after{
    content: '地区选择';
}


</style>