<template>
  <div class="revise_userMsg">
      <div class="login-back" @click='$router.back()' style='color:white'></div>
      <button @click='submit'>提交信息</button>
      <main>
          <ul>
              <li>
                  <span>真实姓名</span>
                  <input type="text" v-model='new_user.real_name'>
              </li>
              <li>
                  <span>新邮箱</span>
                  <input type="text" v-model='new_user.email'>
              </li>
              <li>
                  <span>年龄</span>
                  <input type="text" v-model='new_user.age'>
              </li>
              <li>
                  <span>性别</span>
                  <input type="text" v-model='new_user.gender'>
              </li>
          </ul>
      </main>
  </div>
</template>

<script>
export default {
    name :'ReviseUserMsg',
    props:['user'],
    data(){
        return {
            new_user:{

            }
        }
    },
    methods : {
        submit(){
            console.log(this.new_user)
            let userMsg = 'email=' + this.new_user.email + '&' + 'gender=' + this.new_user.gender + '&' + 
            'real_name=' + this.new_user.real_name + '&' + 'age=' + this.new_user.age + '&' + 'account=' + this.new_user.account
            this.$http.post('http://10.8.236.179:8081/api/reviseUserMsg',userMsg).then(
                response=>{
                    if(response.data != ''){
                        this.$router.back()
                        setTimeout(()=>{
                            alert('修改成功')
                        },310)
                    }else{
                        console.log('修改失败')
                    }
                },
                error=>{
                    console.log(error.message)
                }
            )
        }
    },
    mounted(){
        this.new_user = this.user
    }
}
</script>

<style lang='less'>
.revise_userMsg{
    position: absolute; top:0;left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.377);
    transition: all .3s ease-out;
    z-index: 5;
    button{
        position: absolute ; top: 5vw; right : 10vw;
        width: 30vw;
        height: 10vw;
        color: white;
        background: skyblue;
        border-radius: 5px;
        box-shadow: 0 0 5px skyblue;
    }
    main{
        position: absolute; left: 50%; top : 20vw;
        transform : translate(-50%,0);
        width: 80vw;
        height: 90vw;
        background: white;
        box-shadow: 0 0 5px rgb(255, 255, 255);
        border-radius: 5px;
    }
    ul li{
        position: relative;
        margin:10vw 0 0 10vw;
        width: 60vw;
        height: 10vw;
        box-shadow: 0 0 5px rgb(224, 224, 224);
        span{
            display: inline-block;
            position: absolute; top: -8vw;left: 0;
            width: 30vw;
            height: 8vw;
            text-align: left;
            color: skyblue;
            font-size: 3vw;
            line-height: 8vw;
        }
        input{
            position: absolute; top: 0; left: 0;
            margin-left: 5vw;
            width: 50vw;
            height: 100%;
        }
    }
    
}
</style>