<template>
  <div class="individual">
    <div class="individual-header">
      <div class="individula-header-set" @click='set'></div>
      <router-link to='/login'><div class="individula-header-loginHeadImage" v-show='!user.status'>
        
        </div></router-link>
      <div class="individula-header-headImage" v-show='user.status' @click='setHeader'><span v-show='user.headUrl=="9d589f239e4d11d86734540db915e242播放按钮光明.png"'>设置头像</span>
        <img :src='require("../../../../../../Desktop/阿炳影视服务器/router/upload/"+user.headUrl)'
        alt="" style='width:100%;height:100%'
         v-show='user.headUrl!="9d589f239e4d11d86734540db915e242播放按钮光明.png"'
        >
      </div>
      <input type="file" ref="setHeadimg" accept="image/*" multiple required style='display:none' @change='getFile($event)'>
      <!-- <div class="individual-header-user" v-show='!status'>请先登录</div> -->
      <div class="individual-header-user">{{user.nickname}}</div>
      <div class="individual-header-message">
        <div class="individual-myCoin">&nbsp;我的炳币:{{user.coin}}&nbsp;<span style='color:rgb(255, 208, 0)'></span></div>
      </div>
    </div>
    <div class="individual-body">
      <ul>
        <li><div></div><span>我的收藏</span></li>
        <li  @click='myArticle'><div></div><span>我的帖子</span></li>
        <li><div></div><span>我的粉丝</span></li>
        <li @click='howManyLikes'><div></div><span>收到的赞</span></li>
        <li><div></div><span>我的钱包</span></li>
        <li @click='vip'><div id='vip'>VIP</div><span>VIP特权</span></li>
      </ul>
    </div>
    <!-- 判断是否登录，登录之后才可以放行 -->
    <div class="promptToLogin" v-show='promptToLoginShow'>
      <main>
        <div class="close" @click='promptToLoginShow = false'></div>
        <header>当前你还没有登录</header>
        <button @click='$router.push("/login")'>立即登录</button>
      </main>
    </div>
    <!-- 查看自己收到的赞 -->
    <div class="checkMyGetLikes"></div>
    <transition name='individual'>
      <router-view
        :user='user'
        :getLikesNumber = 'getLikesNumber'
      />
    </transition>
  </div>
</template>

<script>
export default {
    name : 'Individual',
    props :['user'],
    data(){
      return {
        promptToLoginShow : false,

        getLikesNumber : []

        // headImg_url :''
      }
    },
    methods : {
      set(){
        this.$router.push({name:'set_config'})
        console.log(this.user)
      },
      myArticle(){
        if(this.user.status == false){
          this.promptToLoginShow = true
        }else{
          this.$router.push('/individual/my_article')
        }
      },
      vip(){
        this.$router.push('/individual/vip')
      },
      //设置用户头像按钮
      setHeader(){
        this.$refs.setHeadimg.click()
      },
      //头像设置
      getFile(e){
        let file = e.target.files[0]
        var formData = new FormData()
        formData.append('user_headImage',file)
        formData.set('account',this.user.account)
        console.log(formData.get('user_headImage'))
        this.$http.post('http://10.8.236.179:8081/api/setUserHeadImage',formData).then(
            response=>{
                alert('头像上传成功');
                this.$bus.$emit('reviseUserHead',response.data[0])
            },
            error=>{
              console.log(error.message)
            }
          )
      },
      //查看自己收到的赞的个数
      howManyLikes(){
        if(this.user.status == false){
          alert('请先登录');
        }else{
          this.$http.get(`http://10.8.236.179:8081/api/getLikeNumber?user_phone=${this.user.account}`).then(
            response=>{
              this.getLikesNumber = response.data
              this.$router.push('/individual/get_likes')
            },
            error=>{
              console.log(error.message)
            }
          )
        }
      }
    },
    watch :{

    },
    mounted(){
      // if(this.user.status == false){
      //   console.log('请登录')
      // }else{
      //   setTimeout(()=>{
      //     this.headImg_url = "../../../../../../Desktop/阿炳影视服务器/router/upload/9d589f239e4d11d86734540db915e242播放按钮光明.png"
      //   },100)
      // }
    }
}
</script>

<style>
.individual{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 90vh;
  background: rgb(255, 255, 255);
  overflow: hidden;
}
.individual-header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 40vh;
  background: rgb(25, 182, 255);
}
.individula-header-set{
  position: absolute;
  right: 2vh;
  top: 2vh;
  width: 20px;
  height: 20px;
  font-size: 20px;
  color: white;
}
.individula-header-loginHeadImage,.individula-header-headImage{
  position: absolute;
  left: 50%;
  top : 30%;
  transform : translate(-50%,-50%);
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: url('../../assets/用户未登录.jpg')no-repeat;
  background-size: cover;
}
.individula-header-headImage{
  text-align: center;
  line-height: 100px;
  color: blue;
  font-weight: bolder;
  background: white;
}
.individula-header-headImage img{
  position: absolute; top:0;left: 0;
  border-radius: 50%;
}
.individual-header-user{
  position: absolute; left: 50%; top : 60%;
  transform : translate(-50%,-50%);
  width: 150px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: white;
  font-size: 17px;
}
.individual-header-message{
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 10vh;
  background: rgba(255, 255, 255, 0.507);
}
.individual-myCoin{
  width: 50vw;
  height: 100%;
  line-height: 10vh;
  color: white;
  font-size: 2.5vh;
  /* background: black; */
}
.individual-body{
  position: absolute; top: 40vh; left: 0;
  width: 100vw;
  height: 50vh;
}
.individual-body ul li{
  box-sizing: border-box;
  position: relative;
  float: left;
  margin: 4vw 0 0 4vw;
  width: 20vw;
  height: 25vw;
  border: 0.5vw solid skyblue;
  border-radius: 5vw;
}
.individual-body ul li span{
  display: inline-block;
  position: absolute; bottom: 1vw; left: 0;
  width: 19vw;
  height: 5vw;
  color: skyblue;
  font-weight: bolder;
  text-align: center;
  line-height: 5vw;
}
.individual-body div{
  position: absolute; left: 50%; top : 45%;
  transform : translate(-50%,-50%);
  width: 16vw;
  height: 16vw;
  font-size: 8vw;
  text-align: center;
  color: skyblue;
  line-height: 16vw;
}
#vip{
  background: linear-gradient(to right,rgb(255, 206, 45),rgb(255, 146, 57),white);
  background-clip:text;
  color: transparent;
}
.individual-enter,.individual-leave-to{
  transform: translateX(100vw);
}
.individual-leave,.individual-enter-to{
    transform:translateX(0)
}

</style>